html,
body,
#app {
  margin: 0;
  padding: 0;
  font-family: Helvetica;
  font-size: 14px;
  font-weight: 100;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  /* by default, react-native crops content */
}
span {
  display: inline-block;
}
#app .view {
  box-sizing: border-box;
  position: relative;
  border: 0 solid black;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  flex: 0 0 auto;
}
#app .view.pointer-events-none,
#app .view.pointer-events-box-none,
#app .view.pointer-events-box-only * {
  pointer-events: none;
}
#app .view.pointer-events-box-none *,
#app .view.pointer-events-box-only {
  pointer-events: all;
}
#app .text {
  display: inline-block;
}
#app .text.suppress-highlighting {
  user-select: none;
}
